import { LOADING_UI, CLEAR_ERRORS, SET_ERRORS, SET_ADMIN_DATA, CLEAR_ADMIN_DATA, STOP_LOADING, SET_SUCCESS_ADMIN_SUBMIT, CLEAR_ADMIN_SUCCESS, CLEAR_SUCCESS_PUBLIC_SUBMIT, CLEAR_SUCCESS_STATE } from '../types';
